.tabs {
  margin: 5rem 0rem;
  
  @media (max-width: (768px)) {
    margin: 2rem 0rem;
  }
}

.tabs__nav {
  display: flex;
}

.tabs__nav-btn {
  display: flex;
  align-items: center;
  width: 100%;
  font-size: 20px;
  border: 0;
  cursor: pointer;
  background: none;
  outline: none;

  @media (max-width: (768px)) {
    justify-content: center;
  }
}

.tabs__nav-btn:hover {

  & > .tab__title {
    color: #f39e61;
  }

  & > .tabs__icon {
    fill: #f39e61;
  }
}

.tabs__nav-btn.active {
  
  & > .tab__title {
    font-weight: 700;
    color: #f39e61;
  }

  & > .tabs__icon {
    fill: #f39e61;
  }
}

.tabs__content {
  margin-top: 1.5rem;
}

.tabs__item {
  display: none;
}

.tabs__item.active {
  display: block;
}

.tab__title {
  font-size: 1.8rem;
  color: $black;
  font-weight: 400;

  &--active {
    font-weight: 700;
    color: #f39e61;
  }

  @media (max-width: (991px)) {
    font-size: 1.6rem;
  }

  @media (max-width: (768px)) {
    display: none;
  }
}

.tabs__icon {
  height: 25px;
  width: 25px;
  fill: $black;
  margin-right: 1.5rem;
  
  &:hover {
    fill: #f39e61;
  }

  &--active {
    fill: #f39e61;
  }

  @media (max-width: (991px)) {
    margin-right: 1rem;
  }

  @media (max-width: (768px)) {
    height: 23px;
    width: 23px;
  }

  @media (max-width: (499px)) {
    height: 20px;
    width: 20px;
  }
}

.tabs__content-title {
  display: none;

  @media (max-width: (768px)) {
    display: block;
    font-size: 1.8rem;
    color: $black;
    font-weight: 700;
    margin: 2rem 0rem;
  }
}

.tabs__inner {
  display: flex;
  flex-wrap: wrap;



  @media (max-width: (1199px)) {
    justify-content: center;
  }

  @media (max-width: (499px)) {
    margin-right: 0;
  }
}

.tabs__inner-item {
  width: calc(25% - 20px);
  margin-right: 2rem;
  margin-bottom: 2rem;

  @media (max-width: (1199px)) {
    margin-right: 3rem;
    margin-bottom: 1rem;
  }

  @media (max-width: (991px)) {
    width: calc(33.33333% - 10px);
  }

  @media (max-width: (768px)) {
    width: calc(40% - 10px);
  }

  @media (max-width: (499px)) {
    width: calc(100% - 20px);
    margin-right: 0rem;
  }
}

.tabs__inner-img {
  min-width: 100%;
}

.tabs__inner-wrapper {
  position: relative;

  &::after {
    content: 'боевик';
    display: block;
    position: absolute;
    bottom: 5px;
    left: 5px;
    color: $white;
    padding: .8rem 1.3rem;
    border-radius: 5px;
    font-size: 1.6rem;
    cursor: pointer;
    z-index: 2;

    @media (max-width: (1199px)) {
      font-size: 1.3rem;
      padding: .6rem 1.1rem;
    }
  
    @media(max-width: (991px)) {
      font-size: 1rem;
      padding: .4rem .9rem;
    }
  
    @media (max-width: (768px)) {
      font-size: .9rem;
      padding: .3rem .8rem;
    }
  }

  &:hover {

    .tabs__item-title > a {
      color: red;
    }
  }
}

.tabs__item-title {
  position: relative;
  margin-top: 1.2rem;
  display: flex;
  
  & > a {
    color: $black;
    font-size: 1.6rem;
    margin-right: 1rem;
    width: 90%;
    font-weight: 700;

    &:hover {
      color: red;
    }

    @media (max-width: (1199px)) {
      font-size: 1.5rem;
    }
  
    @media(max-width: (991px)) {
      font-size: 1.4rem;
    }
  
    @media (max-width: (768px)) {
      font-size: 1.3rem;
    }

    
  @media (max-width: (499px)) {
    font-size: 1.5em;
  }
  }
}

.tabs__item-age {
  position: absolute;
  right: 0;
  padding: 2px 3px;
  font-size: 12px;
  font-weight: 700;
  color: #9c9c9c;
  border: 1px solid #9c9c9c;
  border-radius: 2px;
}

.tabs__item-date {
  display: flex;
  align-items: center;
  margin-top: 1rem;
  font-size: 1.6rem;
  color: $black;

  & > a {
    margin-left: 1rem;
  }
}