.header__mobile {
  display: none;

  @media (max-width: (768px)) {
    display: block;
  }
}

.header__mobile-wrapper {
  position: relative;
}

.header__mobile-btn {
  position: absolute;
  right: -10px;
  top: -28px;
  border: none;
  z-index: 100;
  background: none;

  display: none;

  @media (max-width: (768px)) {
    display: block;
  }
}

.header__mobile-menu {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #0084d9;
  z-index: 999;
  top: 0;
  left: 0;
  overflow: auto;


  &--opened {
    display: block;
  }

  &--active {
    height: 100%;
  }
}

.header__mobile-icon {
  width: 40px;
  height: 40px;
  fill: $white;

  &--close {
    width: 20px;
    height: 20px;
    fill: $white;
  }
}

.header__mobile-close {
  position: fixed;
  right: 15px;
  top: 13px;
  border: none;
  background: none;
  transition: transform 0.2s linear;

  &:hover {
    transform: rotate(15deg);
  }
}

.header__mobile-ul {
  margin-top: 5rem;
  text-align: center;

  @media (max-width: (499px)) {
    margin-top: 4rem;
  }
}

.header__mobile-item {
  border-bottom: 2px solid #1c70b8;

  &:last-child {
    border: none;
  }
}

.header__mobile-link {
  display: block;
  font-size: 2rem;
  color: $white;
  padding: 2rem;
}
