.main__nav {
  padding: 2rem 1rem 2rem;
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
  align-items: center;
  border-bottom: solid 1px #f2f2f2;

  @media (max-width: (499px)) {
    border-bottom: none;
  }
}

.main__nav-left {
  display: flex;
  align-items: center;

  @media (max-width: (499px)) {
    width: 100%;
  }
}

.main__nav-right {
  display: flex;
  align-items: center;

  @media (max-width: (991px)) {
    display: none;
  }
}

.main__nav-link {
  position: relative;
  color: $black;

  &:hover {
    color: red;
  }
}

.main__nav-item {
  position: relative;
  color: $black;
  font-size: 1.8rem;
  margin-right: 2.5rem;

  &:last-child {
    margin-right: 0;
  }

  &--active {
    font-weight: 700;
    position: relative;
    
    & > .main__nav-link {
      position: relative;
      cursor:text;
      color: $black;
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      height: 2px;
      width: 100%;
      border-bottom: 2px solid #0096ff;
      bottom: -26px;
    }
  }

  @media (max-width: (991px)) {
    font-size: 1.7rem;
  }

  @media (max-width: 768px) {
    margin-right: 1rem;
    font-size: 1.5rem;
  }
  
  @media (max-width: (768px)) {
    &:nth-child(4) {
      display: none;
    }
    &:nth-child(5) {
      display: none;
    }
    &:nth-child(6) {
      display: none;
    }
    &:nth-child(4) {
      display: none;
    }
  }
}

.main__nav-word {
  font-size: 1.8rem;
  font-family: 'PT Sans';
  font-weight: 700;
  text-transform: uppercase;
  color: $black;
  margin-right: 3.5rem;

  &:hover {
    color: red;
  }
}

.main__nav-btn {
  background-color: #0095fc;
  border: none;
  padding: 0rem 1.5rem;
  border-radius: .5rem;
  font-size: 1.6rem;
  cursor: pointer;

  &:hover {
    background-color: #0077c9;
  }
}

.main__btn-link {
  color: $white;
  line-height: 2.8rem;
}

.main__nav-search {
  width: 18px;
  height: 18px;
  border: none;
  cursor: pointer;
  background: none;

  @media (max-width: (768px)) {
    display: none;
  }
}

.main__search-icon {
  position: relative;
  top: -1px;
  height: 18px;
  width: 18px;
}

.main__mobile {
  display: none;

  @media (max-width: (768px)) {
    display: block;
    right: 0px;
    width: 100%;
  }
}

.main__mobile-wrapper {
  position: relative;
  top: -10px;
}

.main__mobile-btn {
  position: absolute;
  right: 0;
  border: none;
  font-size: 1.5rem;
  z-index: 100;
  background: none;

  display: none;

  @media (max-width: (768px)) {
    display: block;
    right: -50px;
  }

  @media (max-width: (499px)) {
    right: 0;
  }
}

.main__mobile-menu {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #0084d9;
  z-index: 999;
  left: 0;
  top: 0;

  &--opened {
    display: block;
  }
}

.main__mobile-icon {
  width: 40px;
  height: 40px;
  fill: $white;

  &--close {
    width: 20px;
    height: 20px;
    fill: $white;
  }
}

.main__mobile-close {
  position: absolute;
  right: 15px;
  top: 13px;
  border: none;
  background: none;
  transition: transform 0.2s linear;

  &:hover {
    transform: rotate(15deg);
  }
}

.main__mobile-ul {
  margin-top: 5rem;
  text-align: center;
}

.main__mobile-item {
  border-bottom: 2px solid #1c70b8;

  &:last-child {
    border: none;
  }
}

.main__mobile-link {
  display: block;
  font-size: 2rem;
  color: $white;
  padding: 2rem;
}