.cinema__wrapper {
  display: flex;

  @media (max-width: (991px)) {
    flex-wrap: wrap;
  }
}

.cinema__left {
  width: 70%;

  @media (max-width: (991px)) {
    width: 100%;
  }
}

.cinema__item {
  margin-top: 4rem;
  margin-bottom: 4rem;
}

.cinema__block {
  display: flex;
  margin-bottom: 1rem;

  @media (max-width: (768px)) {
    flex-wrap: wrap;
  }
}

.cinema__block-photo {
  width: 40%;

  @media (max-width: (991px)) {
    margin-right: 1rem;
  }

  @media (max-width: (768px)) {
    width: 100%;
    margin-bottom: 1rem;
  }
}

.cinema__block-info {
  width: 60%;

  @media (max-width: (768px)) {
    width: 100%;
  }
}

.cinema__block-title {
  display: inline;
  margin: 0;
  padding: 0;
  font-size: 3rem;
  color: $black;
  font-weight: 700;

  &:hover {
    color: red;
  }
}

.cinema__block-marks {
  margin-top: 2rem;
  display: flex;
  align-items: center;
}

.cinema__buy-ticket {
  background-color: #ee721a;
  padding: 0.5rem 1rem;
  font-size: 1.6rem;
  color: $white;
  border-radius: 5px;
  margin-right: 1rem;
}

.cinema__buy-age {
  font-weight: 700;
  color: $black;
  background-color: #f1f1f1;
  border-radius: 5px;
  padding: 0.6rem;
  font-size: 1.6rem;
  margin-right: 1rem;
}

.cinema__block-comment {
  display: flex;
  align-items: center;
}

.cinema__buy-type {
  font-size: 16px;
  color: #999;
  margin-right: 1rem;
}

.cinema__comment-icon {
  width: 15px;
  height: 15px;
  fill: #999;
  margin-right: 1rem;
}

.cinema__comment-count {
  font-size: 16px;
  color: #999;
  font-weight: 400;
}

.cinema__block-text {
  font-size: 18px;
  line-height: 29px;
  color: #000;
}

.cinema__block-dates {
  display: flex;

  @media (max-width: (768px)) {
    flex-wrap: wrap;
  }
}

.cinema__dates {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}

.cinema__name {
  font-size: 1.8rem;
  font-weight: 700;
  color: $black;
  margin-right: 3rem;

  @media (max-width: (499px)) {
    margin-right: 1rem;
  }
}

.cinema__date-item {
  background: none;
  border: none;
  color: $black;
  padding: 0.2rem 0.7rem;
  border: 1px solid #ccc;
  border-radius: 2px;
  margin-right: 1rem;
  font-size: 1.6rem;

  &:hover {
    background-color: #ee721a;
    color: $white;
    cursor: pointer;
  }

  &:last-child {
    margin-right: 0;
  }

  @media (max-width: (768px)) {
    margin-bottom: 1rem;
  }

  @media (max-width: (499px)) {
    margin-right: 0;
  }
}

.cinema__right {
  width: 30%;

  @media (max-width: (991px)) {
    width: 100%;
  }
}

.cinema__right-block {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-left: 2rem;

  @media (max-width: (991px)) {
    flex-direction: row;
    margin-left: 0;
  }
}

.cinema__right-item {
  display: flex;
  flex-direction: column;
  margin-bottom: 3rem;
  margin-right: 2rem;
  width: 100%;

  @media (max-width: (991px)) {
    width: calc(50% - 20px);
  }

  @media (max-width: (499px)) {
    width: 100%;
    margin-right: -2rem;
  }
}

.cinema__right-title {
  color: $black;
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 1rem;
  margin-top: 1rem;

  &:hover {
    color: red;
  }
}

.cinema__right-text {
  font-size: 16px;
  color: $black;
  font-weight: 400;
}

.cinema__right-comment {
  font-size: 14px;
  color: #999;
  margin-top: 1rem;

  &:hover {
    color: red;
  }
}

.cinema__right-link {

  & > img {
    width: 100%;
  }
}

.cinema__card-title {
  font-size: 3rem;
  color: $black;
}

.cinema__card-labels {
  margin-top: 2rem;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;

  @media (max-width: (768px)) {
    flex-wrap: wrap;
  }
}

.cinema__card-buy {
  background-color: #ee721a;
  padding: 0.8rem 1.3rem;
  color: $white;
  border-radius: 5px;
  margin-right: 1rem;
  font-size: 1.6rem;

  @media (max-width: (768px)) {
    margin-right: 2rem;
    margin-bottom: 2rem;
  }
}

.cinema__card-return {
  background-color: #fff;
  color: $black;
  padding: 0.8rem 1.3rem;
  border-radius: 5px;
  border: 1px solid #000;
  margin-right: 1rem;
  font-size: 1.6rem;

  @media (max-width: (768px)) {
    margin-right: 2rem;
    margin-bottom: 2rem;
  }

  &:hover {
    background-color: #ee721a;
  }
}

.cinema__card-comment {
  background-color: #fff;
  color: $black;
  padding: 0.8rem 1.3rem;
  border-radius: 5px;
  border: 1px solid #000;
  margin-right: 1rem;
  font-size: 1.6rem;

  @media (max-width: (768px)) {
    margin-right: 2rem;
    margin-bottom: 2rem;
  }

  &:hover {
    border-color: #ee721a;
  }
}

.cinema__card-time {
  font-size: 1.6rem;
  color: #000;
  background-color: #f1f1f1;
  border-radius: 5px;
  padding: 0.8rem 1.3rem;
  border: 1px solid transparent;
  margin-right: 1rem;
  font-weight: 700;

  @media (max-width: (768px)) {
    margin-right: 2rem;
    margin-bottom: 2rem;
  }
}

.cinema__card-age {
  font-size: 1.6rem;
  color: #000;
  background-color: #f1f1f1;
  border-radius: 5px;
  padding: 0.8rem 1.3rem;
  border: 1px solid transparent;
  margin-right: 1rem;
  font-weight: 700;

  @media (max-width: (768px)) {
    margin-right: 2rem;
    margin-bottom: 2rem;
  }
}

.cinema__card-type {
  color: #999;
  font-size: 1.6rem;
  margin-right: 1rem;

    @media (max-width: (768px)) {
    margin-right: 2rem;
    margin-bottom: 2rem;
  }
}

.cinema__card-country {
  color: #999;
  font-size: 1.6rem;

    @media (max-width: (768px)) {
    margin-right: 2rem;
    margin-bottom: 2rem;
  }
}

.cinema__card-video {
  
  & > iframe {
    width: 100%;
    height: 500px;
  }

  @media (max-width: (768px)) {

    & > iframe {
      width: 100%;
      height: 350px;
    }
  }
}

.cinema__card__desc {
  display: flex;
  justify-content: space-between;
  
  @media (max-width: (991px)) {
    flex-wrap: wrap;
  }
}

.cinema__desc-left {
  width: 65%;

  @media (max-width: (991px)) {
    width: 100%;
  }
}

.cinema__desc-right {
  margin-top: 2rem;
  width: 30%;

  @media (max-width: (991px)) {
    margin-bottom: 2rem;
  }
}

.cinema__desc-text {
  font-size: 1.8rem;
  line-height: 1.3;
  margin-bottom: 3rem;

  &:last-child {
    margin-bottom: 0;
  }
}

.cinema__desc-date {
  margin-top: 1rem;
  margin-bottom: 3px;
  font-weight: 700;
  font-size: 1.8rem;
}

.cinema__desc-error {
  margin-top: 2rem;
  margin-bottom: 2rem;
  font-weight: 400;
  color: #999;
}

.cinema__desc-right {
  flex-direction: column;
  display: flex;

  @media (max-width: (991px)) {
    width: 100%;
    align-items: center;
  }
}

.cinema__share {
  display: flex;

  & > a {
    margin-right: 1rem;

    &:last-child {
      margin-right: 0;
    }
  }
}

.cinema__desc-share {
  width: 34px;
  height: 34px;
}

.cinema__authors {
  margin-top: 2rem;

  @media (max-width: (991px)) {
    display: flex;

    & > div {
      margin-right: 2rem;
      margin-left: 2rem;
    }
  }
}

.cinema__author {
  font-size: 1.4rem;
  font-weight: 700;
  color: $black;
  margin-bottom: 1rem;
}

.cinema__author-name {
  font-size: 1.4rem;
  font-weight: 400;
  color: $black;
  margin-bottom: 3rem;

  &:last-child {
    margin-bottom: 0;
  }
}

.cinema__actors {
  font-size: 1.4rem;
  font-weight: 700;
  color: $black;
  margin-bottom: 1rem;
  margin-top: 1rem;

  @media (max-width: (991px)) {
    margin-top: 0;
  }
}

.cinema__actors-name {
  font-size: 1.4rem;
  color: $black;
  margin-bottom: 0.5rem;
  font-weight: 400;

  &:last-child {
    margin-bottom: 0;
  }
}

.cinema__film-link {
  font-size: 1.4rem;
}

.cinema__dates {
  display: flex;

  @media (max-width: (991px)) {
    width: 100%;
    justify-content: space-around;
  }
}

.cinema__desc-dates {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.cinema__price {
  margin-left: 2rem;
  white-space: nowrap;

  @media (max-width: (991px)) {
    margin-left: 0;
    white-space: normal;
  }
}

.cinema__price-title {
  font-size: 1.4rem;
  color: $black;
  font-weight: 400;
}