.calendar__nav {
  display: flex;
  align-items: center;

  @media (max-width: (768px)) {
    flex-wrap: wrap;
  }
}

.calendar__nav-inner {
  display: flex;

  @media (max-width: (768px)) {
    width: 100%;
  }
}

.calendar__item {
  border: solid 1px #ccc;
  margin-left: -0.1rem;

  &:first-child {
    margin-left: 0;
  }

  &--btn-1 {
    transform: rotate(180deg);
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    
    & > a > svg {
      top: 10px;
      
      
      @media (max-width: (499px)) {
        width: 23px;
        left: -10px;
        top: 7px;
      }
    }
  }

  &--btn-2 {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    
    & > a > svg {
      @media (max-width: (499px)) {
        width: 23px;
        left: -10px;
        top: 7px;
      }
    }
  }
}

.calendar__link {
  text-align: center;
  display: block;
  width: 45px;
  font-size: 1.6rem;
  color: $black;
  padding: 1.1rem 1.5rem;

  &--weekend {
    color: red;
  }

  @media (max-width: (499px)) {
    width: 33px;
    font-size: 1.2rem;
    padding: 0.5rem 1.4rem;
  }

  &--active {
    width: 85px;
    background-color: #fff0ba;
    font-weight: 700;

    @media (max-width: (499px)) {
      width: 62px;
      font-size: 1.2rem;
    }
  }
}

.calendar__next {
  width: 15px;
  height: 14px;
  position: relative;
  top: 9px;

  @media (max-width: (499px)) {
    top: 7px;
  }
}

.calendar__sort {
  margin-left: 4rem;
  display: flex;

  @media (max-width: (768px)) {
    margin-top: 1rem;
    margin-left: 0;
    width: 100%;
  }
}

.calendar__sort-cat {
  display: flex;
  align-items: center;
  height: 15px;
  margin-right: 0.5rem;
  font-size: 1.6rem;
  color: #0095fc;
  border-bottom: dotted 1px #0095fc;

  &--active {
    font-weight: 700;
    color: $black;
    border: none;
  }
}
