.footer {
  background-color: #202020;
  padding-top: 2.5rem;
  padding-bottom: 2rem;
}

.footer__social {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 3.5rem;
  border-bottom: solid 4px #555;

  @media (max-width: (499px)) {
    padding-bottom: 1.5rem;
  }
}

.footer__social-inner {
  display: flex;

  @media (max-width: (768px)) {
    width: 100%;
    justify-content: center;
  }
}

.footer__social-links {
  display: flex;

  @media (max-width: (768px)) {
    width: 100%;
    justify-content: center;
  }
}

.footer__social-inner {
  margin-bottom: 1.5rem;

  @media (max-width: (499px)) {
    margin-bottom: 3rem;
  }
}

.footer__social-item {
  margin: 0rem 1.1rem;
}

.footer__social-icon {
  width: 20px;
  height: 20px;
  fill: $white;

  @media (max-width: (768px)) {
    width: 18px;
    height: 18px;
  }
}

.footer__likns-item {
  color: $white;
  font-size: 1.4rem;
  padding-left: 1rem;

  &:first-child {
    padding-right: 0;
  }

  @media (max-width: (768px)) {
    font-size: 1.2rem;
  }

  @media (max-width: (499px)) {
    font-size: 1rem;
  }
}

.footer__info {
  margin-top: 1.5rem;
}

.footer__info-item {
  display: flex;
  flex-direction: column;
  margin-right: 3rem;

  @media (max-width: (499px)) {
    margin-right: 0;
  }
}

.footer__info {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 3.5rem;

  @media (max-width: (499px)) {
    padding-bottom: 1rem;
  }
}

.footer__info-link {
  color: #dedede;
  font-size: 1.4rem;
  margin-bottom: 0.5rem;

  &:hover {
    color: $white;
  }
}

.footer__info-left {
  display: flex;

  @media (max-width: (768px)) {
    width: 100%;
    justify-content: center;
  }

  @media (max-width: (499px)) {
    flex-direction: column;
    justify-content: center;
    text-align: left;
  }
}

.footer__conf {
  display: flex;
  flex-direction: column;
  width: 35%;

  @media (max-width: (768px)) {
    width: 100%;
    justify-content: center;
  }
}

.footer__info-text {
  font-size: 1.4rem;
  color: #5a5a5a;
}

.footer__credits-text {
  color: #5a5a5a;
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.footer__credits-link {
  color: #dedede;
  font-size: 1.4rem;

  &:hover {
    color: $white;
  }
}

.footer__credits-name {
  color: #dedede;
  font-size: 1.4rem;

  &:hover {
    color: $white;
  }
}